import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import firebase from "gatsby-plugin-firebase";

import { selectApplication } from "../features/application/applicationSlice";
import { getDataItems, getDataSuccess, getDataFailure } from "../features/data/dataSlice"; 

import { useFirebaseNode } from "../hooks/useFirebaseNode";

import FormWithPredefinedIndicators from "./FormWithPredefinedIndicators";

export default function ProjectFormsWithPredefinedIndicators() {
  const dispatch = useDispatch();

  const [status, setStatus] = useState({
    loadingData: false,
    dataArray: undefined,
    errorData: undefined
  });

  const email = useSelector(selectApplication).email;
  const currentProjectTitle = useSelector(selectApplication)
    .currentProjectTitle;
  const currentProjectMediaAndDataAndTemplatesURL = useSelector(
    selectApplication
  ).currentProjectMediaAndDataAndTemplatesURL;

  let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");

  const { data: userPosts, loading, error } = useFirebaseNode(
    "usersCraft/" + userEmail + "/posts/"
  );

  function fetchNow(url) {
    dispatch(getDataItems());
    setStatus({ loadingData: true });
    firebase
      .database()
      .ref('openmediadata/' + url)
      .once("value")
      .then(snapshot => {
        let dataObject = snapshot.val();
        let dataArray = !!dataObject ? Object.keys(dataObject).map((item) => dataObject[item]) : [];
        dispatch(getDataSuccess(dataArray));
        setStatus({ loadingData: false, dataArray });
      })
      .catch((errorData) => {
        dispatch(getDataFailure());
        setStatus({ loadingData: false, errorData });
      });
  }

  React.useEffect(() => {
  //  console.log(currentProjectMediaAndDataAndTemplatesURL);
    if (currentProjectMediaAndDataAndTemplatesURL) {
      fetchNow(currentProjectMediaAndDataAndTemplatesURL);
    }
  }, [currentProjectMediaAndDataAndTemplatesURL]);



  if (!!loading || !userPosts || !!status.loadingData || !status.dataArray) { return <div>...</div> }

  if (!!error) { return <div>:-(</div> }

  let calculations = Object.keys(userPosts)
    .map((item) => userPosts[item])
    .filter(
      (item) =>
        item.type === "calculation" && item.theme === currentProjectTitle
    );

//  console.log(calculations);
//  console.log(status);

  return (
    <div>
      {calculations.map((item) => (
        <FormWithPredefinedIndicators key={item.id} post={item} />
      ))}
    </div>
  );
}
