import React from "react";
import { useSelector, useDispatch } from "react-redux";
import produce from 'immer'
import { useForm } from "react-hook-form";
import firebase from "gatsby-plugin-firebase";

import { selectUserData, createDataItem, editDataItem, doFindValuesForForm, chooseDictionary } from "../features/data/dataSlice"; // empty_data
import { selectApplication } from "../features/application/applicationSlice";
import {
    selectSpreadsheetProtoData,
    selectSpreadsheetData,
    load_data,
    // new_empty_spreadsheet,
    // set_spreadsheetTitle,
    // createProtoArray,
    // createProtoObject
} from "../features/spreadsheet/spreadsheetSlice";


import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";


export default function EditableDataTableWithPredefinedIndicators(props) {
    const userData = useSelector(selectUserData);
    const applicationState = useSelector(selectApplication);
    const content = useSelector(selectSpreadsheetProtoData);
    const calculatedSpreadsheet = useSelector(selectSpreadsheetData);
    const dispatch = useDispatch();
    // const [show, setShow] = useState(false);
    const { register, handleSubmit, watch, reset } = useForm({ defaultValues: doFindValuesForForm(userData.data, props.formMetaData, applicationState.currentProjectTitle, applicationState.currentProjectReportIndicatorsDictionary) });
    // const [chartSeries, setChartSeries] = useState([]);

    // const handleClose = () => setShow(false);
    // const handleShow = () => setShow(true);

    let projectUrl = applicationState.currentProjectMediaAndDataAndTemplatesURL;
    //  console.log(projectUrl);

    function onSubmit(data) {

        let date = new Intl.DateTimeFormat("ru", {
            weekday: "short",
            year: "numeric",
            month: "short",
            day: "numeric",
            hour: "numeric",
            minute: "numeric",
        }).format(new Date());

        let currentDay = new Intl.DateTimeFormat("en", {
            weekday: "short",
            year: "numeric",
            month: "short",
            day: "numeric"
        }).format(new Date()).replace(/[^a-zA-Z0-9]/g, "_");

        let updates = {};

        props.formMetaData.reportIndicators.map(indicator => {
            props.formMetaData.reportDates.map(period => {
                let item = indicator + '___' + period;
                let found = userData.data.find(dataItem => dataItem.form_name === item && dataItem.projectMediaAndDataAndTemplatesURL === projectUrl);
                //         console.log(found);
                if (!found) {
                    //       let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");
                    let idUserDataItem = firebase.database().ref('openmediadata').child(projectUrl).push().key;
                    let foundInDictionary = chooseDictionary(applicationState.currentProjectReportIndicatorsDictionary).filter(dictItem => dictItem.ru_title === indicator);
                    let newDataItem = {
                        id: idUserDataItem,
                        projectTitle: applicationState.currentProjectTitle,
                        projectComment: applicationState.currentProjectComment,
                        projectMediaAndDataAndTemplatesURL: projectUrl,
                        projectSourseDataURL: applicationState.currentProjectSourseDataURL,
                        projectMoneyScale: applicationState.currentProjectMoneyScale,
                        form_name: item,
                        code: indicator,
                        reportIndicatorsDictionary: applicationState.currentProjectReportIndicatorsDictionary,
                        ru_title: foundInDictionary[0].ru_title,
                        ru_comment: foundInDictionary[0].ru_comment,
                        ru_source: foundInDictionary[0].ru_source,
                        period: period,
                        value: data[item],
                        date: date
                    };
                    updates['openmediadata/' + projectUrl + '/' + idUserDataItem] = newDataItem;
                    dispatch(createDataItem(newDataItem));
                }
                else {
                    if (found.value !== data[item]) {
                        //                   console.log('Надо изменить значение ' + item);
                        updates['openmediadata/' + projectUrl + '/' + found.id + '/value'] = data[item];
                        updates['openmediadata/' + projectUrl + '/' + found.id + '/date'] = date;
                        dispatch(editDataItem({ ...found, value: data[item], date: date }));
                    }
                }
                return null
            })
            return null
        })



        updates['/currentLaboratoryDay/' + currentDay + '/projects/' + projectUrl + '/' + props.formUrl] = '+';
        console.log(updates);

        return firebase.database().ref().update(updates)
            .then(() => { console.log('Data saved') })
            .catch(error => console.log(error))
    }

    function doReset() {
        //      console.log('doTransferToSpreadsheet');
        reset({ ...doFindValuesForForm(userData.data, props.formMetaData, applicationState.currentProjectTitle, applicationState.currentProjectReportIndicatorsDictionary) });

    }

    function doTransferToSpreadsheet() {
        //      console.log('doTransferToSpreadsheet');
        //      console.log(watch());
        //      console.log(content);
        const newContent = produce(content, draft => {
            for (var i = 1; i < content.length; i++) {
                var row = content[i];
                for (var j = 1; j < row.length; j++) {
                    let objectKey = content[i][0] + '___' + content[0][j];
                    draft[i][j] = watch(objectKey) || ''; // content[i][j]
                }
            }
        })
        // console.log(newContent)
        dispatch(load_data({ protoData: newContent }));
    }

    function doTransferToForm() {
        //      console.log('doTransferToForm');
        let newFormValues = {}
        for (var i = 0; i < content.length; i++) {
            var row = content[i];
            for (var j = 0; j < row.length; j++) {
                if ((content[i][0].toString().length > 2) && (content[0][j].toString().length > 2)) {
                    let objectKey = content[i][0] + '___' + content[0][j];
                    newFormValues[objectKey] = calculatedSpreadsheet[i][j]//content[i][j]
                }
            }
        }
        //      console.log(newFormValues);
        reset({ ...newFormValues })
    }

    // console.log(props);
    // console.log(applicationState);

    return <div>

        <form onSubmit={handleSubmit(onSubmit)}>

            <InputGroup className="mb-3" size="sm">
                <InputGroup.Prepend><InputGroup.Text style={{ width: '15rem' }}>Показатели</InputGroup.Text></InputGroup.Prepend>
                {props.formMetaData.reportDates.map(item =>
                    <FormControl
                        key={item}
                        placeholder={item}
                        readOnly
                    />
                )}
            </InputGroup>

            {props.formMetaData.reportIndicators.map(row =>
                <InputGroup className="mb-3" size="sm" key={row}>
                    <InputGroup.Prepend><InputGroup.Text style={{ width: '15rem' }}>{row}</InputGroup.Text></InputGroup.Prepend>
                    {props.formMetaData.reportDates.map(period =>
                        <FormControl
                            key={row + '___' + period}
                            name={row + '___' + period}
                            type="number"
                            step="0.1"
                            ref={register}
                            required
                        />
                    )}
                </InputGroup>
            )}

            {applicationState?.currentProjectOpenMediaDataURL && applicationState?.currentProjectOpenMediaDataURL.length > 6 ?
                <div>
                    <Button className="m-1" variant="outline-secondary" type="submit" size="sm">Сохранить</Button>
                    <Button className="m-1" variant="outline-secondary" size="sm" onClick={() => doReset()}>Восстановить</Button>
                    <Button className="m-1" variant="outline-secondary" size="sm" onClick={() => doTransferToSpreadsheet()}>Передать в Расчет</Button>
                    <Button className="m-1" variant="outline-secondary" size="sm" onClick={() => doTransferToForm()}>Передать из Расчета</Button>
                    {/* <Button className="m-1" variant="outline-secondary" size="sm" onClick={() => doOpenModalWithChart()}>Диаграмма</Button> */}
                </div>
                : null}

        </form>
    </div>
}

