import React, { useState } from "react";
import { useSelector } from "react-redux";
import firebase from "gatsby-plugin-firebase";
import { useForm } from "react-hook-form";

import { selectApplication } from "../features/application/applicationSlice";
import { selectUserDataItems, doFindValuesForChartDataSeriesByIndicator } from "../features/data/dataSlice";

// import ApexBarChartsGrouped from "./ApexBarChartsGrouped";
import ApexEmptyOptionsChart from "./ApexEmptyOptionsChart";

import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Alert from "react-bootstrap/Alert";


export default function ApexBarChartsGroupedEditor(props) {
  const applicationState = useSelector(selectApplication);
  const userData = useSelector(selectUserDataItems);

  const [type, setType] = useState(null);
  const [options, setOptions] = useState(null);

  const [showChart, setShowChart] = useState(false);

  const [editPostTitleComment, doEditPostTitleComment] = React.useState(true);
  const [savedSuccessfully, DoSavedSuccessfully] = React.useState(false);

  function setFormObject(objectWithNamesValues) {
    if (applicationState.email.length > 6) {
      let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = firebase.database().ref(userEmail).child("posts").push().key;

      let dataObject = {
        requiredseries: props.reportIndicators,
        requiredcategories: props.categories,
        type,
        options
      }

      let postObject = {
        id: idPost,
        title:
          objectWithNamesValues.title.length > 2
            ? objectWithNamesValues.title
            : "Диаграмма",
        theme: applicationState.currentProjectTitle, //currentProjectMediaAndDataAndTemplatesURL,
        answer: applicationState.currentProjectMediaAndDataAndTemplatesURL,
        comment: objectWithNamesValues.comment, //Тема
        type: "chartwithpredefineddictionarydata",
        content: dataObject,
        quizString: applicationState.currentProjectReportIndicatorsDictionary,
        deleted: false,
        email: applicationState.email,
        user: applicationState.user,
        avatarUrl: !!applicationState?.avatarUrl ? applicationState.avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      console.log(postObject);

      //    dispatch(createPost(postObject));
      var updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/usersTemplates/charts/" + idPost] = postObject;

      updates['/openmedia/' + idPost] = postObject;

      return firebase
        .database()
        .ref()
        .update(updates)
        .then(() => {
          DoSavedSuccessfully(true);
          doEditPostTitleComment(false);
        });
    }
  }

  function setBarsGrouped() {
    setShowChart(false);
    setType('bar');
    setOptions({ chart: { type: 'bar', stacked: false } });
    setShowChart(true);
    ;
  }

  function setBarsGroupedStacked() {
    setShowChart(false);
    setType('bar');
    setOptions({
      chart: {
        type: 'bar',
        stacked: true
      }
    });
    setShowChart(true);
  }

  function setLine() {
    setShowChart(false);
    setType('line');
    setOptions({
      chart: {
        type: 'line',
        zoom: {
          enabled: false
        }
      },
      dataLabels: {
        enabled: false
      },
      stroke: {
        curve: 'straight'
      },
      // title: {
      //   text: 'Product Trends by Month',
      //   align: 'left'
      // },
      grid: {
        row: {
          colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
          opacity: 0.5
        },
      },
      // xaxis: {
      //   categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep'],
      // }
    }
    );
    setShowChart(true);
  }




  let series = props.reportIndicators.map(indicator => {
    return {
      name: indicator, data: doFindValuesForChartDataSeriesByIndicator(userData, indicator, props.categories)
    }
  })

  return (
    <div>
      <div className="m-3 d-flex justify-content-md-around">

        <Button
          onClick={() => { setBarsGrouped() }}
          variant="outline-secondary"
          size="sm">
          Bars
            </Button>
        <Button
          onClick={() => { setBarsGroupedStacked() }}
          variant="outline-secondary"
          size="sm">
          Bars Stacked
            </Button>
        <Button
          onClick={() => { setLine() }}
          variant="outline-secondary"
          size="sm">
          Line
         </Button>

      </div>


      {showChart && !!props.categories && !!series ?
        <ApexEmptyOptionsChart
          categories={props.categories}
          series={series
            //   [{
            //   name: 'Кэш фло',
            //   data: [44, 55, 41, 64, 22, 43, 21],
            // }, {
            //   name: "Дисконтированный кэш-фло",
            //   data: [44, 50, 33, 52, 13, 31, 14]
            // }]
          }
          options={options}
          type={type}

        />
        // <ApexBarChartsGrouped
        //   categories={props.categories}
        //   series={series
        //     //   [{
        //     //   name: 'Кэш фло',
        //     //   data: [44, 55, 41, 64, 22, 43, 21],
        //     // }, {
        //     //   name: "Дисконтированный кэш-фло",
        //     //   data: [44, 50, 33, 52, 13, 31, 14]
        //     // }]
        //   }
        //   options={options}
        //   type={type}
        // />

        : null}

      {editPostTitleComment &&
        !!props.categories &&
        props.categories.length > 0 &&
        !!props.reportIndicators && props.reportIndicators.length > 0 ? (
          <PostFormHook title={""} setFormObject={setFormObject} />
        ) : null}



      {savedSuccessfully ? <Alert variant={"success"}>Успешно!</Alert> : null}

    </div>
  );
}

let modalForm = [
  { name: "title", title: "Название", type: "text" },
  { name: "comment", title: "Комментарий", type: "textarea" },
];

function PostFormHook(props) {
  const { register, handleSubmit, reset } = useForm({ //, watch, errors
    defaultValues: { title: props.title },
  });

  React.useEffect(() => {
    reset({ title: props.title });
  }, [props.title]);

  return (
    <form onSubmit={handleSubmit(props.setFormObject)}>
      {modalForm.map((item) => (
        <InputGroup className="mb-3" size="sm" key={item.name}>
          <InputGroup.Prepend>
            <InputGroup.Text style={{ width: "8rem" }}>
              {item.title}
            </InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            key={item.name}
            name={item.name}
            type={item.type}
            ref={register}
          //       as={item.type === "textarea" ? "textarea" : "input"}
          />
        </InputGroup>
      ))}
      <Button variant="outline-secondary" type="submit" size="sm">
        Сохранить
      </Button>
    </form>
  );
}


